import React from 'react'
import { Layout, Stack, Main } from '@layout'
import PageTitle from '@components/PageTitle'
import Divider from '@components/Divider'
import Seo from '@widgets/Seo'
import ContactForm from '@widgets/ContactForm'

export default props => (
  <Layout {...props}>
    <Seo title='Contact' />
    <Divider />
    <Stack>
      <Main>
        <PageTitle
          header="About us"
          subheader='PAP은 프로덕트 분석가, 데이터 분석가, 데이터 프로덕트 오너 8명이 모여 만든 프로덕트 데이터 분석 커뮤니티입니다. 
          PAP은 Product Analytics Playground 의 약자로, 프로덕트 데이터 분석에 대해 편안하게 이야기하면서 노는 공간을 구성하고자 하는 의도를 담았습니다.'
        />
      </Main>
    </Stack>
  </Layout>
)
